import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import { getUser } from '../../../../selectors/appSelectors'
import { filterEventList } from '../../actions/EventListActions'

import DispatchEventListFilter from './DispatchEventListFilter'

const mapStateToProps = state => ({
  user: getUser(state),
  filterByCat: state.eventList.filterByCat,
  filterByLevel: state.eventList.filterByLevel,
  list: state.eventList.list,
  listPending: state.eventList.listPending,
  listError: state.eventList.listError,
})

const mapDispatchToProps = dispatch => ({
  filterEventList: bindActionCreators(filterEventList, dispatch),
})

class DispatchEventListFilterContainer extends React.PureComponent {
  render() {
    return <DispatchEventListFilter {...this.props} />
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DispatchEventListFilterContainer)
