import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import { getUser } from '../../../../selectors/appSelectors'
import { fetchEventList } from '../../actions/EventListActions'

import DispatchEventList from './DispatchEventList'

const mapStateToProps = state => ({
  user: getUser(state),
  list: state.eventList.list,
  listPending: state.eventList.listPending,
  listError: state.eventList.listError,
})

const mapDispatchToProps = dispatch => ({
  fetchEventList: bindActionCreators(fetchEventList, dispatch),
})

class DispatchEventListContainer extends React.PureComponent {
  componentDidMount() {
    this.props.fetchEventList()
  }

  render() {
    return <DispatchEventList {...this.props} />
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DispatchEventListContainer)
