import React from 'react'

import CheckCircleIcon from '@material-ui/icons/Lens'
import moment from 'moment'

class DispatchEventListItem extends React.PureComponent {
  renderTime(time) {
    if (time.length < 13) {
      // @todo    
      time = `${time}000`
    }
    return moment(Number(time)).format('DD.MM.YY HH:mm')
  }

  render() {
    const { index, style, data: { classes, list } = {} } = this.props
    const item = list.Items[index]
    return (
      <div className={classes.item} style={style}>
        <div className={`${classes.itemColumn} ${classes.itemColumn_level}`}>
          {!item.status ? (
            <CheckCircleIcon className={classes.itemIcon} color="action" />
          ) : item.status.S === 'normal' ? (
            <CheckCircleIcon
              className={classes.itemIcon}
              style={{ color: 'rgb(46,204,113)' }}
            />
          ) : item.status.S === 'almin' ? (
            <CheckCircleIcon
              className={classes.itemIcon}
              style={{ color: 'rgb(252,118,0)' }}
            />
          ) : item.status.S === 'almaj' ? (
            <CheckCircleIcon
              className={classes.itemIcon}
              style={{ color: 'rgb(232,15,15)' }}
            />
          ) : (
            <CheckCircleIcon className={classes.itemIcon} color="action" />
          )}
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_device}`}>
          {item.device.S}
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_message}`}>
          {(item.level.S === 'E')?(<b>:</b>):((item.level.S === 'U')?(<b> : </b>):((item.level.S === 'W')?(':'):((item.level.S === 'I')?(':'):(':'))))}{item.message.S}
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_time}`}>
          {this.renderTime(item.time.N)}
        </div>
      </div>
    )
  }
}

export default DispatchEventListItem
