import React from 'react'

import moment from 'moment'
import ThingStatus from '../../../../components/Thing/ThingStatus'
import ThingIdentity from '../../../../components/Thing/ThingIdentity'
import ThingAlarm from '../../../../components/Thing/ThingAlarm'
import ThingGuard from '../../../../components/Thing/ThingGuard'
import ThingPower from '../../../../components/Thing/ThingPower'
import ThingConnection from '../../../../components/Thing/ThingConnection'
import Link from '@material-ui/core/Link'

class DispatchThingListItem extends React.PureComponent {
  isDisconnectedThing(thing) {
    const ts = new Date(Number(`${thing.msgtime}000`))
    const now = new Date()
    if (now - ts > 1000 * 60 * 60 * 2) {
      return true
    }
    return false
  }

  render() {
    const {
      index,
      style,
      data: { classes, data, addressById, groupById, onClickThing } = {},
    } = this.props
    const thing = data[index]
    const now = new Date()
    const msgtime = Number(`${thing.msgtime}000`)
    const msgtimeMoment = moment(msgtime)
    const disconnected = this.isDisconnectedThing(thing)
    let disconnectedHours
    if (disconnected) {
      if (now - msgtime > 1000 * 60 * 60 * 24) {
        disconnectedHours = 24
      } else if (now - msgtime > 1000 * 60 * 60 * 2) {
        disconnectedHours = 2
      }
    }
    return (
      <div
        className={`${classes.item} ${
          disconnected ? classes.item_noConnection : ''
        }`}
        style={style}
      >
        <div className={`${classes.itemColumn} ${classes.itemColumn_status}`}>
          <ThingStatus thing={thing} hideLabel hideValue />
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_object}`}>
          <Link
            onClick={() => {
              onClickThing(thing)
            }}
          >
            <ThingIdentity
              thing={thing}
              addressById={addressById}
              groupById={groupById}
            />
          </Link>
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_problems}`}>
          <div>
            {disconnected && (
              <strong>&nbsp; &nbsp;{disconnectedHours}</strong>
            )}
            {(thing.alarm === true || thing.alarm === 'true') && (
              <strong> </strong>
            )}
            {thing.rssi && thing.rssi < -92 && (
              <strong>  GSM</strong>
            )}
            {thing.tamper &&
              (thing.tamper === true || thing.tamper === 'true') && (
                <strong> </strong>
              )}
            {thing.pwr && !(thing.pwr === true || thing.pwr === 'true') && (
              <strong>&nbsp;</strong>
            )}
          </div>
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_details}`}>
          <ThingAlarm hideLabel hideValue thing={thing} />
          <ThingGuard hideLabel hideValue thing={thing} />
          <ThingPower hideLabel hideValue thing={thing} />
          <ThingConnection hideLabel hideValue thing={thing} />
          {/*<span>
              -&nbsp;
            {thing.conn === 'eth'
              ? 'Ethernet'
              : thing.conn === 'wifi'
                ? 'Wi-Fi'
                : thing.conn === 'gsm' ? 'GSM' : ''}&nbsp; |
            &nbsp;
            {thing.guard === true || thing.guard === 'true' ? (
              ' '
            ) : (
              <b>  </b>
            )}
            {thing.temperature !== undefined ? (
              <span>&nbsp;| {thing.temperature} C</span>
            ) : null}
          </span>*/}
        </div>
        <div className={`${classes.itemColumn} ${classes.itemColumn_updated}`}>
          <time
            dateTime={msgtimeMoment.format('DD.MM.YY HH:mm')}
            title={msgtimeMoment.format('YYYY-MM-DD HH:mm')}
          >
            {moment
              .duration(msgtime - now)
              .locale('ru')
              .humanize(true)}
          </time>
        </div>
      </div>
    )
  }
}

export default DispatchThingListItem
