import { takeLatest, take, put, fork, call, select } from 'redux-saga/effects'
import { eventChannel } from 'redux-saga'
import {
  ANALYST_DATA_FETCH,
  ANALYST_DATA_FETCH_SUCCESS,
  ANALYST_DATA_FETCH_FAILURE,
} from '../actions/AnalystActions'
import {
  getAwsMqttClient,
  subscribeEvents,
  unsubscribeEvents,
} from '../../../services/AwsMqttService'
import { AWS_CONFIG } from '../../../settings/aws/AwsConfig'
import aws4 from 'aws4'
import { getCsvShka } from '../../../services/CsvShkaService'
import * as getCSV from 'get-csv'
import { getUser } from '../../../selectors/appSelectors'

export default function*() {
  yield takeLatest(ANALYST_DATA_FETCH, fetchAnalystData)
  yield fork(listenEvents)
  yield fork(listenInterval)
}

function* listenEvents() {
  const client = yield call(getAwsMqttClient)
  const channel = new eventChannel(emit => {
    const handleMessage = message => {
      const event = JSON.parse(message.payloadString)
      emit({ event })
    }
    client.on('/events', handleMessage)
    subscribeEvents()
    return () => {
      client.removeListener('/events', handleMessage)
      unsubscribeEvents()
    }
  })
  while (true) {
    yield take(channel)
    yield put({ type: ANALYST_DATA_FETCH })
  }
}

function* listenInterval() {
  let interval
  const channel = new eventChannel(emit => {
    interval = setInterval(() => {
      emit(new Date())
    }, 20 * 1000)
    return () => {
      clearInterval(interval)
    }
  })
  while (true) {
    yield take(channel)
    yield put({ type: ANALYST_DATA_FETCH })
  }
}

function* fetchAnalystData() {
  try {
    const user = yield select(getUser)
    const opts = {
      host: 'iot.us-west-2.amazonaws.com',
      method: 'GET',
      headers: {
        auth: user.credentials.idToken,
      },
    }
    aws4.sign(opts, {
      accessKeyId: user.credentials.accessKeyId,
      secretAccessKey: user.credentials.secretAccessKey,
      sessionToken: user.credentials.sessionToken,
    })
    const res = yield call(fetch, AWS_CONFIG.restApi + '/things/active', opts)
    const activeThings = yield call([res, 'json'])
    const data = yield call(getCsvShka, 'st_eye_dataset_laststatus')
    const dataUri = data['entries'][0]['dataURI']
    const dataset = yield call(getCSV, dataUri)
    yield put({
      type: ANALYST_DATA_FETCH_SUCCESS,
      payload: {
        activeThings,
        dataset,
      },
    })
  } catch (error) {
    yield put({
      type: ANALYST_DATA_FETCH_FAILURE,
      payload: error,
    })
  }
}
