import Paho from 'paho-mqtt'
import * as AWS from 'aws-sdk'
import { AWS_CONFIG } from '../settings/aws/AwsConfig'

import EventEmitter from 'events'

function SigV4getSignatureKey(key, date, region, service) {
  var kDate = AWS.util.crypto.hmac('AWS4' + key, date, 'buffer')
  var kRegion = AWS.util.crypto.hmac(kDate, region, 'buffer')
  var kService = AWS.util.crypto.hmac(kRegion, service, 'buffer')
  var kCredentials = AWS.util.crypto.hmac(kService, 'aws4_request', 'buffer')
  return kCredentials
}

function SigV4getSignedUrl(host, region, credentials) {
  var datetime = AWS.util.date.iso8601(new Date()).replace(/[:-]|\.\d{3}/g, '')
  var date = datetime.substr(0, 8)

  var method = 'GET'
  var protocol = 'wss'
  var uri = '/mqtt'
  var service = 'iotdevicegateway'
  var algorithm = 'AWS4-HMAC-SHA256'

  var credentialScope = `${date}/${region}/${service}/aws4_request`
  var canonicalQuerystring = `X-Amz-Algorithm=${algorithm}&X-Amz-Credential=${encodeURIComponent(
    credentials.accessKeyId + '/' + credentialScope
  )}&X-Amz-Date=${datetime}&X-Amz-SignedHeaders=host`
  var canonicalHeaders = `host:${host}\n`
  var payloadHash = AWS.util.crypto.sha256('', 'hex')
  var canonicalRequest =
    method +
    '\n' +
    uri +
    '\n' +
    canonicalQuerystring +
    '\n' +
    canonicalHeaders +
    '\nhost\n' +
    payloadHash

  var stringToSign =
    algorithm +
    '\n' +
    datetime +
    '\n' +
    credentialScope +
    '\n' +
    AWS.util.crypto.sha256(canonicalRequest, 'hex')
  var signingKey = SigV4getSignatureKey(
    credentials.secretAccessKey,
    date,
    region,
    service
  )
  var signature = AWS.util.crypto.hmac(signingKey, stringToSign, 'hex')

  canonicalQuerystring += '&X-Amz-Signature=' + signature
  if (credentials.sessionToken) {
    canonicalQuerystring +=
      '&X-Amz-Security-Token=' + encodeURIComponent(credentials.sessionToken)
  }

  var requestUrl = protocol + '://' + host + uri + '?' + canonicalQuerystring
  return requestUrl
}

class AwsMqttClient extends EventEmitter {
  constructor(client) {
    super()
    this.ready = false
    this.client = client
    this.client.onMessageArrived = message => {
      this.emit(message.destinationName, message)
    }
    this.client.onConnectionLost = res => {
      this.ready = true
      console.error(res)
    }
    this.client.onConnected = reconnect => {
      this.ready = true
    }
  }
}

let awsmMqttClientPromise = null
let awsMqttClient = null

export function getAwsMqttClient() {
  if (awsMqttClient) {
    return Promise.resolve(awsMqttClient)
  }
  if (awsmMqttClientPromise) {
    return awsmMqttClientPromise
  }
  const requestUrl = SigV4getSignedUrl(
    AWS_CONFIG.endpointAddress,
    AWS.config.region,
    AWS.config.credentials
  )
  const client = new Paho.Client(requestUrl, AWS_CONFIG.clientId)
  return (awsmMqttClientPromise = new Promise((resolve, reject) => {
    client.connect({
      reconnect: true,
      onSuccess: () => {
        awsMqttClient = new AwsMqttClient(client)
        awsmMqttClientPromise = null
        resolve(awsMqttClient)
      },
      onFailure: (...args) => {
        console.log('connection error', ...args)
      },
    })
  }))
}

export function subscribeEvents({ timeout = 10 } = {}) {
  return new Promise((onSuccess, onFailure) => {
    awsMqttClient.client.subscribe(`/events`, { onSuccess, onFailure, timeout })
  })
}

export function unsubscribeEvents({ timeout = 10 } = {}) {
  return new Promise((onSuccess, onFailure) => {
    awsMqttClient.client.unsubscribe(`/events`, {
      onSuccess,
      onFailure,
      timeout,
    })
  })
}

export function subscribeThingShadowGet(thing, { timeout = 10 } = {}) {
  if (!awsMqttClient.ready) {
    return Promise.reject(Error('Disconnected'))
  }
  return Promise.all([
    new Promise((onSuccess, onFailure) => {
      awsMqttClient.client.subscribe(
        `$aws/things/${thing.device}/shadow/get/accepted`,
        { onSuccess, onFailure, timeout }
      )
    }),
    new Promise((onSuccess, onFailure) => {
      awsMqttClient.client.subscribe(
        `$aws/things/${thing.device}/shadow/get/rejected`,
        { onSuccess, onFailure, timeout }
      )
    }),
  ])
}

export function subscribeThingShadowUpdate(thing, { timeout = 10 } = {}) {
  if (!awsMqttClient.ready) {
    return Promise.reject(Error('Disconnected'))
  }
  return Promise.all([
    new Promise((onSuccess, onFailure) => {
      awsMqttClient.client.subscribe(
        `$aws/things/${thing.device}/shadow/update/accepted`,
        { onSuccess, onFailure, timeout }
      )
    }),
    new Promise((onSuccess, onFailure) => {
      awsMqttClient.client.subscribe(
        `$aws/things/${thing.device}/shadow/update/rejected`,
        { onSuccess, onFailure, timeout }
      )
    }),
  ])
}

export function requestThingShadowGet(thing) {
  const message = new Paho.Message('')
  message.destinationName = `$aws/things/${thing.device}/shadow/get`
  awsMqttClient.client.send(message)
}

export function requestThingShadowUpdate(thing, thingShadow) {
  const message = new Paho.Message(JSON.stringify(thingShadow))
  message.destinationName = `$aws/things/${thing.device}/shadow/update`
  awsMqttClient.client.send(message)
}

