import React from 'react'
import Button from '@material-ui/core/Button'
import { Link } from 'react-router-dom'

const LinkBtn = ({ to, label }) => {
  return (
    <Link to={to}>
      <Button variant="contained" color="primary">
        <span>{label}</span>
      </Button>
    </Link>
  )
}

export default LinkBtn
