export const THING_LIST_FETCH = 'THING_LIST_FETCH'
export const THING_LIST_FETCH_SUCCESS = 'THING_LIST_FETCH_SUCCESS'
export const THING_LIST_FETCH_FAILURE = 'THING_LIST_FETCH_FAILURE'

export const THING_OPEN = 'THING_OPEN'
export const THING_CLOSE = 'THING_CLOSE'

export function fetchThingList() {
  return {
    type: THING_LIST_FETCH,
  }
}

export function openThing(thing) {
  return {
    type: THING_OPEN,
    payload: thing,
  }
}

export function closeThing() {
  return {
    type: THING_CLOSE,
  }
}
