import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { fetchThingList, openThing } from '../../actions/ThingListActions'
import DispatchThingList from './DispatchThingList'

const mapStateToProps = state => ({
  thingListParams: state.dispatch.filter.params,
  thingList: state.thingList.data,
  thingListPending: state.thingList.dataPending,
  thingListError: state.thingList.dataError,
  addressById: state.addressList.list ? state.addressList.listItemById : null,
  groupById: state.groupsList.list ? state.groupsList.listItemById : null,
})

const mapDispatchToProps = dispatch => ({
  onFetchThingList: bindActionCreators(fetchThingList, dispatch),
  onClickThing: bindActionCreators(openThing, dispatch),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DispatchThingList)
