export const ANALYST_DATA_FETCH = 'ANALYST_DATA_FETCH'
export const ANALYST_DATA_FETCH_SUCCESS = 'ANALYST_DATA_FETCH_SUCCESS'
export const ANALYST_DATA_FETCH_FAILURE = 'ANALYST_DATA_FETCH_FAILURE'

export function fetchAnalystData() {
  return async (dispatch, getState) => {
    dispatch({
      type: ANALYST_DATA_FETCH,
      status: 'pending',
    })
  }
}
