import { AWS_CONFIG } from '../../../settings/aws/AwsConfig'

export const EVENT_LIST_FETCH = 'EVENT_LIST_FETCH'
export const EVENT_LIST_FILTER = 'EVENT_LIST_FILTER'
export const EVENT_LIST_APPEND = 'EVENT_LIST_APPEND'

export function fetchEventList(user) {
  function getTime(time) {
    if (time.length < 13) {
      // @todo    
      time = `${time}000`
    }
    return Number(time)
  }

  return async dispatch => {
    dispatch({
      type: EVENT_LIST_FETCH,
      status: 'pending',
    })
    const res = await fetch(AWS_CONFIG.restApi + '/log')
    const data = await res.json()
    if (res.ok) {
      data.Items.sort((a, b) => {
        return getTime(b.time.N) - getTime(a.time.N)
      })
      dispatch({
        type: EVENT_LIST_FETCH,
        status: 'success',
        payload: data,
      })
    } else {
      dispatch({
        type: EVENT_LIST_FETCH,
        status: 'failure',
        payload: data,
      })
    }
  }
}

export function filterEventList({ filterByCat, filterByLevel }) {
  return async dispatch => {
    dispatch({
      type: EVENT_LIST_FILTER,
      payload: {
        filterByCat,
        filterByLevel,
      },
    })
  }
}
