import React from 'react'
import { withStyles } from '@material-ui/styles'
import Chip from '@material-ui/core/Chip'

class DispatchEventListFilter extends React.PureComponent {
  state = {}

  render() {
    const { classes, filterByCat, filterByLevel, filterEventList } = this.props
    return (
      <div>
        <Chip
          className={classes.chip}
          label=" "
          color={filterByCat === null ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat: null,
              filterByLevel: null,
            })
          }}
        />

        <Chip className={classes.titleChip} label=":" />
        <Chip
          className={classes.chip}
          label=""
          color={filterByCat === 'con' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat: filterByCat !== 'con' ? 'con' : null,
              filterByLevel,
            })
          }}
        />
        <Chip
          className={classes.chip}
          label=""
          color={filterByCat === 'sec' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat: filterByCat !== 'sec' ? 'sec' : null,
              filterByLevel,
            })
          }}
        />
        <Chip
          className={classes.chip}
          label=""
          color={filterByCat === 'oth' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat: filterByCat !== 'oth' ? 'oth' : null,
              filterByLevel,
            })
          }}
        />

        <Chip className={classes.titleChip} label=":" />
        <Chip
          className={classes.chip}
          label=""
          color={filterByLevel === 'U' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat,
              filterByLevel: filterByLevel !== 'U' ? 'U' : null,
            })
          }}
        />
        <Chip
          className={classes.chip}
          label=""
          color={filterByLevel === 'E' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat,
              filterByLevel: filterByLevel !== 'E' ? 'E' : null,
            })
          }}
        />
        <Chip
          className={classes.chip}
          label=""
          color={filterByLevel === 'W' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat,
              filterByLevel: filterByLevel !== 'W' ? 'W' : null,
            })
          }}
        />
        <Chip
          className={classes.chip}
          label=""
          color={filterByLevel === 'I' ? 'secondary' : undefined}
          onClick={() => {
            filterEventList({
              filterByCat,
              filterByLevel: filterByLevel !== 'I' ? 'I' : null,
            })
          }}
        />
      </div>
    )
  }
}

const styles = theme => ({
  root: {},
  chip: {
    marginLeft: 8,
    '&:first-child': {
      marginLeft: 24,
    },
  },
  titleChip: {
    marginLeft: 24,
    background: 'transparent',
    '& > span': {
      padding: 0,
    },
  },
})

export default withStyles(styles)(DispatchEventListFilter)
