import { takeLatest, put, call } from 'redux-saga/effects'
import {
  USER_CHECK,
  USER_CHECK_SUCCESS,
  USER_CHECK_FAILURE,
} from '../actions/AuthActions'
import {
  USER_AUTH,
  USER_AUTH_SUCCESS,
  USER_AUTH_FAILURE,
} from '../actions/AuthActions'
import { authenticate } from '../services/AwsService'
import Cookies from 'universal-cookie'

import * as AWS from 'aws-sdk'
import { AWS_CONFIG } from '../settings/aws/AwsConfig'
import { getAwsMqttClient } from '../services/AwsMqttService'

const cookies = new Cookies()

export default function*() {
  yield takeLatest(USER_CHECK, checkUser)
  yield takeLatest(USER_CHECK_SUCCESS, initUser)
  yield takeLatest(USER_AUTH, authUser)
  yield takeLatest(USER_AUTH_SUCCESS, initUser)
}

function* checkUser() {
  try {
    const credentials = cookies.get('credentials')
    if (credentials) {
      const { name, idToken, accessToken } = credentials
      const user = yield call(resolveUser, name, idToken, accessToken)
      yield put({
        type: USER_CHECK_SUCCESS,
        payload: user,
      })
    } else {
      yield put({
        type: USER_CHECK_FAILURE,
        payload: '',
      })
    }
  } catch (err) {
    yield put({
      type: USER_CHECK_FAILURE,
      payload: err,
    })
  }
}

function* authUser({ payload }) {
  try {
    const name = payload.username
    const session = yield call(authenticate, payload)
    const idToken = session.getIdToken().getJwtToken()
    const idTokenExpires = session.getIdToken().getExpiration()
    const accessToken = session.getAccessToken().getJwtToken()
    cookies.set(
      'credentials',
      {
        name,
        idToken,
        accessToken,
      },
      {
        path: '/',
        expires: new Date(idTokenExpires * 1000),
      }
    )
    const user = yield call(resolveUser, name, idToken, accessToken)
    yield put({
      type: USER_AUTH_SUCCESS,
      payload: user,
    })
  } catch (err) {
    yield put({
      type: USER_AUTH_FAILURE,
      payload: err,
    })
  }
}

function* initUser() {
  yield call(getAwsMqttClient)
}

async function resolveUser(name, idToken, accessToken) {
  AWS.config.update({ region: AWS_CONFIG.region }) //   ?
  const loginCredentialsProvider = `cognito-idp.${AWS_CONFIG.region}.amazonaws.com/${AWS_CONFIG.userPoolId}`
  AWS.config.credentials = new AWS.CognitoIdentityCredentials({
    IdentityPoolId: AWS_CONFIG.identityPoolId,
    Logins: {
      [loginCredentialsProvider]: idToken,
    },
  })
  await new Promise((resolve, reject) => {
    AWS.config.credentials.get(error => {
      if (error) {
        AWS.config.credentials.clearCachedId()
        return reject(error)
      }
      resolve()
    })
  })
  const IotConfig = {
    endpoint: 'iot.' + AWS_CONFIG.region + '.amazonaws.com',
    region: AWS_CONFIG.region,
    accessKeyId: AWS.config.credentials.accessKeyId,
    accessToken: accessToken,
    idToken: idToken,
    secretAccessKey: AWS.config.credentials.secretAccessKey,
    sessionToken: AWS.config.credentials.sessionToken,
  }
  new AWS.Iot(IotConfig) //   ?
  return {
    name,
    credentials: IotConfig,
  }
}
