import {
  ANALYST_DATA_FETCH,
  ANALYST_DATA_FETCH_SUCCESS,
  ANALYST_DATA_FETCH_FAILURE,
} from '../actions/AnalystActions'

const initialState = {
  data: null,
  dataPending: false,
  dataError: null,

  total: {},
  groups: [],
}

export default (state = initialState, action) => {
  switch (action.type) {
    case ANALYST_DATA_FETCH: {
      return {
        ...state,
        dataPending: true,
      }
    }

    case ANALYST_DATA_FETCH_SUCCESS: {
      const { activeThings, dataset } = action.payload
      const total = {
        count: dataset.length || 0,
        statusNormalCount: 0,
        statusAlminCount: 0,
        statusAlmajCount: 0,
        noConnectionCount: 0,
        errorCount: 0,
        errorPercentage: 0,
      }
      const groups = Object.values(
        dataset.reduce((groupById, row) => {
          const group = (groupById[row.group_id] = groupById[row.group_id] || {
            id: row.group_id,
            thingById: {},
            count: 0,
            statusNormalCount: 0,
            statusAlminCount: 0,
            statusAlmajCount: 0,
            noConnectionCount: 0,
            errorCount: 0,
            errorPercentage: 0,
          })
          if (!group.thingById[row.device]) {
            group.thingById[row.device] = row
          } else {
            const prevRow = group.thingById[row.device]
            if (parseInt(row.__time) >= parseInt(prevRow.__time)) {
              group.thingById[row.device] = row
            }
          }
          return groupById
        }, {})
      )
      const now = new Date()
      for (const group of groups) {
        for (const thing of Object.values(group.thingById)) {
          group.count++
          const msgtime = new Date(Number(`${thing.__time}000`))
          if (now - msgtime > 1000 * 60 * 60 * 2) {
            total.noConnectionCount++
            group.noConnectionCount++
            total.errorCount++
            group.errorCount++
          } else {
            switch (thing.status) {
              case 'normal': {
                total.statusNormalCount++
                group.statusNormalCount++
                break
              }
              case 'almin': {
                total.statusAlminCount++
                group.statusAlminCount++
                total.errorCount++
                group.errorCount++
                break
              }
              case 'almaj': {
                total.statusAlmajCount++
                group.statusAlmajCount++
                total.errorCount++
                group.errorCount++
                break
              }
              default: {
                //  
              }
            }
          }
        }
        group.errorPercentage = Math.round(
          (group.errorCount / group.count) * 100
        )
      }
      total.errorPercentage = Math.round((total.errorCount / total.count) * 100)
      return {
        ...state,
        dataPending: false,
        data: action.payload,
        total,
        groups,
      }
    }

    case ANALYST_DATA_FETCH_FAILURE: {
      return {
        ...state,
        dataPending: false,
        dataError: action.payload,
      }
    }

    default: {
      return state
    }
  }
}
