import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import Dispatch from './Dispatch'

import DispatchEventListContainer from '../DispatchEventList/DispatchEventList.container'
import DispatchEventListFilterContainer from '../DispatchEventListFilter/DispatchEventListFilter.container'

import DispatchThingListContainer from '../DispatchThingList/DispatchThingList.container'
import DispatchThingListFilter from '../DispatchThingListFilter/DispatchThingListFilter.container'
import DispatchThingDialogContainer from '../DispatchThingDialog/DispatchThingDialog.container'

import { getUser } from '../../../../selectors/appSelectors'

import { fetchAddressList } from '../../../../actions/AddressListActions'
import { fetchGroupsList } from '../../../../actions/GroupsListActions'

const mapStateToProps = state => ({
  user: getUser(state),
  addressList: state.addressList.list,
  groupList: state.groupsList.list,
  eventListComponent: DispatchEventListContainer,
  eventListFilterComponent: DispatchEventListFilterContainer,
  thingListComponent: DispatchThingListContainer,
  thingListFilterComponent: DispatchThingListFilter,
  thingDialogComponent: DispatchThingDialogContainer,
})

const mapDispatchToProps = dispatch => ({
  onFetchAddressList: bindActionCreators(fetchAddressList, dispatch),
  onFetchGroupList: bindActionCreators(fetchGroupsList, dispatch),
})

export default connect(mapStateToProps, mapDispatchToProps)(Dispatch)
