import { AWS_CONFIG } from '../settings/aws/AwsConfig'

export function authenticate(params) {
  return new Promise((resolve, reject) => {
    var $ = require('jquery')
    var AmazonCognitoIdentity = require('amazon-cognito-identity-js')

    var authenticationData = {
      Username: params.username.toLowerCase(), // your username here
      Password: params.password, // your password here
    }
    var poolData = {
      UserPoolId: AWS_CONFIG.userPoolId,
      ClientId: AWS_CONFIG.clientId,
    }

    console.log(poolData)
    var userPool = new AmazonCognitoIdentity.CognitoUserPool(poolData)
    var userData = {
      Username: authenticationData.Username,
      Pool: userPool,
    }
    var authenticationDetails = new AmazonCognitoIdentity.AuthenticationDetails(
      authenticationData
    )
    var cognitoUser = new AmazonCognitoIdentity.CognitoUser(userData)

    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: function(session) {
        resolve(session)
      },

      onFailure: function(err) {
        console.log(err)
        console.log('AWS AUTH ERROR')
        $('.box') // lol
          .addClass('shake')
          .delay(1000)
          .queue(function() {
            $('.box').removeClass('shake')
            $("input[name='password']")
              .focus()
              .select()
            $('.box').dequeue()
          })
        resolve({
          status: false,
          code: 'invalid_login_or_password',
          credentials: {},
        })
      },

      //newPasswordRequired: function(userAttributes, requiredAttributes) {
      //  // User was signed up by an admin and must provide new
      //  // password and required attributes, if any, to complete
      //  // authentication.
      //  console.log('user: ', userAttributes)
      //  console.log('required: ', requiredAttributes)
      //  // the api doesn't accept this field back
      //  delete userAttributes.email_verified
      //  delete userAttributes.phone_number_verified
      //  var newPassword = prompt('Input new password', '')
      //  // store userAttributes on global variable
      //  userAttributes.name = 'name' // TODO
      //  userAttributes.family_name = 'surname' // TODO
      //  var sessionUserAttributes = userAttributes
      //  console.log(userAttributes)
      //  cognitoUser.completeNewPasswordChallenge(
      //    newPassword,
      //    sessionUserAttributes,
      //    {
      //      onSuccess: result => {
      //        console.log('NEW PASSWORD COMPLETED: ')
      //        console.log(result)
      //      },
      //      onFailure: err => {
      //        console.log(err)
      //        reject(err)
      //      },
      //    }
      //  )
      //  // store userAttributes on global variable
      //  sessionUserAttributes = userAttributes
      //},
    })
  })
}
