import Cookies from 'universal-cookie' // WTF
import {
  USER_CHECK,
  USER_CHECK_SUCCESS,
  USER_CHECK_FAILURE,
} from '../actions/AuthActions'
import {
  USER_AUTH,
  USER_AUTH_SUCCESS,
  USER_AUTH_FAILURE,
} from '../actions/AuthActions'
import { USER_EXIT } from '../actions/AuthActions'
const cookies = new Cookies()

const initialState = {
  user: null,
  userError: null,
  userCheckPending: false,
  userAuthPending: false,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case USER_CHECK: {
      return {
        ...state,
        user: null,
        userCheckPending: true,
      }
    }
    case USER_CHECK_SUCCESS: {
      return {
        ...state,
        userCheckPending: false,
        userError: null,
        user: {
          name: action.payload.name,
          credentials: action.payload.credentials,
        },
      }
    }
    case USER_CHECK_FAILURE: {
      return {
        ...state,
        userCheckPending: false,
        user: null,
        userError: action.payload,
      }
    }

    case USER_AUTH: {
      return {
        ...state,
        user: null,
        userAuthPending: true,
      }
    }
    case USER_AUTH_SUCCESS: {
      return {
        ...state,
        userAuthPending: false,
        userError: null,
        user: {
          name: action.payload.name,
          credentials: action.payload.credentials,
        },
      }
    }
    case USER_AUTH_FAILURE: {
      return {
        ...state,
        userAuthPending: false,
        user: null,
        userError: action.payload,
      }
    }

    case USER_EXIT: {
      cookies.remove('credentials')
      return {
        ...state,
        user: null,
        userError: null,
      }
    }

    default: {
      return state
    }
  }
}
