import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { closeThing } from '../../actions/ThingListActions'

import DispatchThingDialog from './DispatchThingDialog'
import DispatchThingDetailsContainer from '../DispatchThingDetails/DispatchThingDetails.container'

const mapStateToProps = (state, props) => ({
  open: !!state.thingList.thing,
  thing: state.thingList.thing,
  thingDetailsComponent: DispatchThingDetailsContainer,
  addressById: state.addressList.listItemById,
  groupById: state.groupsList.listItemById,
})

const mapDispatchToProps = dispatch => ({
  onClose: bindActionCreators(closeThing, dispatch),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DispatchThingDialog)
