import React, { useState } from 'react'

const Auth = ({ onAuth }) => {
  const [data, setData] = useState({
    username: '',
    password: '',
  })

  return (
    <div>
      <form
        className="box"
        onSubmit={evt => {
          evt.preventDefault()
          onAuth(data)
        }}
      >
        <h1>ST-EYE</h1>
        <input
          type={'text'}
          onChange={({ target: { value: username } }) => {
            setData({ ...data, username })
          }}
          placeholder={' '}
          value={data.username}
        />
        <input
          type={'password'}
          onChange={({ target: { value: password } }) => {
            setData({ ...data, password })
          }}
          placeholder={''}
          value={data.password}
        />
        <button type="submit">Login</button>
      </form>
    </div>
  )
}

export default Auth
