import React from 'react'
import { withStyles } from '@material-ui/styles'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import Container from '@material-ui/core/Container'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
//import DialogActions from '@material-ui/core/DialogActions'
//import Button from '@material-ui/core/Button'
import ThingIdentity from '../../../../components/Thing/ThingIdentity'

class DispatchThingDialog extends React.PureComponent {
  render() {
    const {
      classes,
      open,
      onClose,
      thing,
      thingDetailsComponent: ThingDetails,
      addressById,
      groupById,
    } = this.props
    return (
      <Dialog
        classes={{
          container: classes.dialogContainer,
          paper: classes.dialogPaper,
        }}
        open={open}
        onClose={onClose}
        maxWidth="md"
        fullWidth
      >
        <AppBar
          className={classes.dialogBar}
          position="static"
          color="primary"
          elevation={0}
        >
          <Container maxWidth={false}>
            <Toolbar disableGutters>
              <Typography
                className={classes.dialogBarTitle}
                variant="h6"
                color="inherit"
              >
                {' '}
                <ThingIdentity
                  thing={thing}
                  addressById={addressById}
                  groupById={groupById}
                />
              </Typography>

              <IconButton color="inherit" edge="end" onClick={onClose}>
                <CloseIcon color="inherit" />
              </IconButton>
            </Toolbar>
          </Container>
        </AppBar>

        <DialogContent className={classes.dialogContent}>
          <Container maxWidth={false}>
            {open && thing && <ThingDetails thing={thing} />}
          </Container>
        </DialogContent>

        {/*<DialogActions className={classes.dialogActions}>
          <Button
            className={classes.dialogAction}
            variant="outlined"
            color="default"
            onClick={onClose}
          >
            
          </Button>
        </DialogActions>*/}
      </Dialog>
    )
  }
}

const styles = theme => ({
  root: {},
  dialogContainer: {
    alignItems: 'start',
  },
  dialogPaper: {
    marginTop: 56 + 16,
  },
  dialogBar: {
    backgroundColor: 'rgba(25,25,25,1)', // @todo    
  },
  dialogBarTitle: {
    flexGrow: 1,
  },
  dialogContent: {
    padding: theme.spacing(3, 0),
  },
  dialogActions: {
    padding: theme.spacing(1),
    justifyContent: 'flex-start',
  },
  dialogAction: {
    padding: theme.spacing(1, 3),
  },
})

export default withStyles(styles)(DispatchThingDialog)
