import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { fetchAnalystData } from '../../actions/AnalystActions'
import { fetchGroupsList } from '../../../../actions/GroupsListActions'

import Analyst from './Analyst'
import AnalystGroupListContainer from '../AnalystGroupList/AnalystGroupList.container'

const mapStateToProps = state => ({
  groupListComponent: AnalystGroupListContainer,
})

const mapDispatchToProps = dispatch => ({
  onFetchData: bindActionCreators(fetchAnalystData, dispatch),
  onFetchGroupList: bindActionCreators(fetchGroupsList, dispatch),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Analyst)
