import React from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import LinkBtn from '../../components/LinkBtn'
import UserContainer from '../../containers/UserContainer'
import AnalystModule from '../../modules/Analyst/packages/Analyst/Analyst.module'
import WsSelectContainer from '../../containers/WsSelectContainer'
import DispatchModule from '../../modules/Dispatch/packages/Dispatch/Dispatch.module'
import NotFound from '../../components/NotFound'
import './App.css'
import Auth from '../Auth/Auth.container'

const App = ({ user, userPending }) => (
  <div className="rootDiv">
    <header className="header">
      <div className="logo">
        <span className="appName">ST-Eye</span>
        <span className="separator"></span>
        <span className="pageName"></span>
      </div>
      <div className="top-menu">
        <LinkBtn to="/ws" label={'  '} />
      </div>
      <UserContainer />
    </header>
    {userPending ? (
      '...'
    ) : user ? (
      <Switch>
        <Route exact path="/">
          <Redirect to="/ws" />
        </Route>
        <Route path="/login">login</Route>
        <Route path="/analyst" component={AnalystModule} />
        <Route path="/dispatch" component={DispatchModule} />
        <Route path="/ws" component={WsSelectContainer} />
        <Route component={NotFound} />
      </Switch>
    ) : (
      <Switch>
        <Route path="/" component={Auth} />
      </Switch>
    )}
  </div>
)

export default App
