import { takeLatest, take, put, fork, call, select } from 'redux-saga/effects'
import { eventChannel } from 'redux-saga'
import {
  THING_LIST_FETCH,
  THING_LIST_FETCH_SUCCESS,
  THING_LIST_FETCH_FAILURE,
} from '../actions/ThingListActions'
import {
  EVENT_LIST_APPEND,
  EVENT_LIST_FILTER,
} from '../actions/EventListActions'
import {
  getAwsMqttClient,
  subscribeEvents,
  unsubscribeEvents,
} from '../../../services/AwsMqttService'
import { getCsvShka } from '../../../services/CsvShkaService'
import * as getCSV from 'get-csv'

export default function*() {
  yield takeLatest(THING_LIST_FETCH, fetchThingList)
  yield fork(listenEvents)
  yield fork(listenInterval)
}

function* listenEvents() {
  const client = yield call(getAwsMqttClient)
  const channel = new eventChannel(emit => {
    const handleMessage = message => {
      const event = JSON.parse(message.payloadString)
      emit({ event })
    }
    client.on('/events', handleMessage)
    subscribeEvents()
    return () => {
      client.removeListener('/events', handleMessage)
      unsubscribeEvents()
    }
  })
  while (true) {
    const { event } = yield take(channel)
    yield put({ type: THING_LIST_FETCH })
    yield put({ type: EVENT_LIST_APPEND, payload: event })
    const { filterByCat, filterByLevel } = yield select(state => ({
      filterByCat: state.eventList.filterByCat,
      filterByLevel: state.eventList.filterByLevel,
    }))
    yield put({
      type: EVENT_LIST_FILTER,
      payload: {
        filterByCat,
        filterByLevel,
      },
    })
  }
}

function* listenInterval() {
  let interval
  const channel = new eventChannel(emit => {
    interval = setInterval(() => {
      emit(new Date())
    }, 20 * 1000)
    return () => {
      clearInterval(interval)
    }
  })
  while (true) {
    yield take(channel)
    yield put({ type: THING_LIST_FETCH })
    const { filterByCat, filterByLevel } = yield select(state => ({
      filterByCat: state.eventList.filterByCat,
      filterByLevel: state.eventList.filterByLevel,
    }))
    yield put({
      type: EVENT_LIST_FILTER,
      payload: {
        filterByCat,
        filterByLevel,
      },
    })
  }
}

function* fetchThingList() {
  try {
    const data = yield call(getCsvShka, 'st_eye_dataset_dispatch')
    const dataUri = data['entries'][0]['dataURI']
    const thingsList = yield call(getCSV, dataUri)
    yield put({
      type: THING_LIST_FETCH_SUCCESS,
      payload: thingsList,
    })
  } catch (err) {
    yield put({
      type: THING_LIST_FETCH_FAILURE,
      payload: err,
    })
  }
}
