import React from 'react'
import { withStyles } from '@material-ui/styles'

import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import Box from '@material-ui/core/Box'
import CircularProgress from '@material-ui/core/CircularProgress'
import Switch from '@material-ui/core/Switch'

class ThingDout extends React.PureComponent {
  isItemDesired(i) {
    const {
      thingShadow: {
        state: { desired, reported },
      },
    } = this.props
    if (desired) {
      const desiredDout = desired.dout
      const reportedDout = reported.dout
      if (desiredDout && desiredDout[i].value !== reportedDout[i].value) {
        return true
      }
    }
    return false
  }

  getItemValue(i) {
    const { thingShadow, unsavedState } = this.props
    if (unsavedState !== undefined) {
      return unsavedState[i].value
    } else {
      if (thingShadow.state.desired && thingShadow.state.desired.dout) {
        return thingShadow.state.desired.dout[i].value
      } else if (
        thingShadow.state.reported &&
        thingShadow.state.reported.dout
      ) {
        return thingShadow.state.reported.dout[i].value
      }
    }
  }

  render() {
    const { classes, thing, thingShadow, onChangeState } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <div className={classes.root}>
        <Table className={classes.table}>
          <TableHead>
            <TableRow>
              <TableCell style={{ width: '70%' }}></TableCell>
              <TableCell></TableCell>
              <TableCell />
            </TableRow>
          </TableHead>
          <TableBody>
            {!!thingShadow &&
              !!thingShadow.state.reported.dout &&
              thingShadow.state.reported.dout.map((item, i) => (
                <TableRow key={i}>
                  <TableCell component="th" scope="row">
                    {item.name}
                  </TableCell>
                  <TableCell>{this.getItemValue(i)}</TableCell>
                  <TableCell>
                    <Box display="inline-flex" alignItems="center">
                      {this.isItemDesired(i) && (
                        <CircularProgress style={{ width: 20, height: 20 }} />
                      )}
                      <Switch
                        color={this.isItemDesired(i) ? 'primary' : 'secondary'}
                        disabled={!thingShadow}
                        checked={!!this.getItemValue(i)}
                        onChange={({ target: { checked: on } }) => {
                          onChangeState(i, on)
                        }}
                      />
                    </Box>
                  </TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </div>
    )
  }
}

const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  table: {
    marginTop: theme.spacing(0),
    marginBottom: theme.spacing(3),
    '& thead th': {
      padding: theme.spacing(0, 2),
      lineHeight: '24px',
      '&:first-child': {
        paddingLeft: 0,
      },
    },
    '& tbody th, & tbody td': {
      height: 48,
      padding: theme.spacing(0, 2),
      lineHeight: '24px',
      '&:first-child': {
        paddingLeft: 0,
      },
    },
    '& tbody th': {
      width: '37%',
    },
    '& tbody td:last-child': {
      width: 1,
      paddingRight: 0,
      textAlign: 'right',
    },
    '& > tbody > tr:last-child th, & > tbody > tr:last-child td': {
      borderBottom: 0,
    },
  },
})

export default withStyles(styles)(ThingDout)
