import { ADDRESS_LIST_FETCH } from '../actions/AddressListActions'

const initialState = {
  list: null,
  listPending: false,
  listError: null,
  listItemById: {},
}

export default (state = initialState, action) => {
  switch (action.type) {
    case ADDRESS_LIST_FETCH: {
      const { status, payload } = action

      return {
        ...state,
        list: status === 'success' ? payload : null,
        listPending: status === 'pending',
        listError: status === 'failure' ? payload : null,
        listItemById:
          status === 'success'
            ? payload.Items.reduce((listItemById, item) => {
                listItemById[item.id.N] = item
                return listItemById
              }, {})
            : null,
      }
    }

    default: {
      return state
    }
  }
}
