import {
  EVENT_LIST_FETCH,
  EVENT_LIST_FILTER,
  EVENT_LIST_APPEND,
} from '../actions/EventListActions'

const initialState = {
  listRaw: null,
  list: null,
  listPending: false,
  listError: null,
  filterByCat: null,
  filterByLevel: null,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case EVENT_LIST_FETCH: {
      const { status, payload } = action
      return {
        ...state,
        listRaw: status === 'success' ? payload : null,
        list: status === 'success' ? payload : null,
        listPending: status === 'pending',
        listError: status === 'failure' ? payload : null,
      }
    }

    case EVENT_LIST_FILTER: {
      const {
        payload: { filterByCat, filterByLevel },
      } = action
      const Items = state.listRaw.Items.filter(item => {
        let filtered = true
        if (filterByCat) {
          filtered = filterByCat === item.cat.S
        }
        if (filterByLevel) {
          filtered = filterByLevel === item.level.S
        }
        return filtered
      })
      return {
        ...state,
        list: {
          ...state.list,
          Items,
        },
        filterByCat,
        filterByLevel,
      }
    }

    case EVENT_LIST_APPEND: {
      const { payload } = action
      return {
        ...state,
        listRaw: {
          ...state.listRaw,
          Count: state.listRaw.Count + 1,
          Items: [createItem(payload), ...state.listRaw.Items],
        },
      }
    }

    default: {
      return state
    }
  }
}

function createItem(data) {
  // lol
  return {
    cat: { S: data.cat },
    device: { S: data.device },
    level: { S: data.level },
    message: { S: data.message },
    status: { S: data.status },
    time: { N: data.time },
  }
}
