import React from 'react'
import { withStyles } from '@material-ui/styles'

class ThingIdentity extends React.PureComponent {
  render() {
    const { classes, thing, addressById, groupById } = this.props
    if (!thing) {
      return null
    }

    return (
      <span className={classes.root} title={thing.device}>
        {!!thing.address_id &&
        !!addressById &&
        !!addressById[thing.address_id] &&
        !!addressById[thing.address_id].comment ? (
          <span>{addressById[thing.address_id].comment.S}</span>
        ) : (
          <span></span>
        )}
        {!!thing.address_id &&
        !!addressById &&
        !!addressById[thing.address_id] ? (
          <span>{addressById[thing.address_id].name.S}</span>
        ) : (
          <span></span>
        )}
        {!!thing.group_id && !!groupById && !!groupById[thing.group_id] ? (
          <span>{groupById[thing.group_id].name.S}</span>
        ) : (
          <span></span>
        )}
      </span>
    )
  }
}

const styles = theme => ({
  root: {
    '& > span': {
      '&:not(:last-child)::after': {
        display: 'inline',
        content: '", "',
      },
    },
  },
})

export default withStyles(styles)(ThingIdentity)
