import {
  THING_LIST_FETCH,
  THING_LIST_FETCH_SUCCESS,
  THING_LIST_FETCH_FAILURE,
  THING_OPEN,
  THING_CLOSE,
} from '../actions/ThingListActions'

const initialState = {
  data: null,
  dataError: null,
  dataPending: false,

  thing: null,
}

export default (state = initialState, action) => {
  switch (action.type) {
    case THING_LIST_FETCH: {
      return {
        ...state,
        data: null,
        dataError: null,
        dataPending: true,
      }
    }

    case THING_LIST_FETCH_SUCCESS: {
      return {
        ...state,
        data: action.payload,
        dataError: null,
        dataPending: false,
      }
    }

    case THING_LIST_FETCH_FAILURE: {
      return {
        ...state,
        data: null,
        dataError: action.payload,
        dataPending: false,
      }
    }

    case THING_OPEN: {
      const { payload: thing } = action
      return {
        ...state,
        thing,
      }
    }

    case THING_CLOSE: {
      return {
        ...state,
        thing: null,
      }
    }

    default: {
      return state
    }
  }
}
