import React from 'react'
import { withStyles } from '@material-ui/styles'

import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'

class Dispatch extends React.Component {
  state = {
    thing: null,
  }

  componentDidMount() {
    const { user, onFetchAddressList, onFetchGroupList } = this.props
    onFetchAddressList()
    onFetchGroupList(user)
  }

  componentWillUnmount() {}

  render() {
    const {
      classes,
      thingListFilterComponent: ThingListFilter,
      eventListComponent: EventList,
      eventListFilterComponent: EventListFilter,
      thingListComponent: ThingList,
      thingDialogComponent: ThingDialog,
    } = this.props
    return (
      <Box className={classes.root} display="flex" flexGrow={1}>
        <ThingListFilter />

        <Box flexGrow={1} display="flex" flexDirection="column" pt={2} pb={2}>
          <Paper
            component={Box}
            height="62%"
            display="flex"
            flexDirection="column"
            mb={2}
          >
            <Toolbar>
              <Typography variant="h6"> </Typography>
            </Toolbar>

            <ThingList />
          </Paper>

          <Paper
            component={Box}
            flexGrow={1}
            display="flex"
            flexDirection="column"
          >
            <Toolbar>
              <Typography variant="h6"> </Typography>
              <EventListFilter />
            </Toolbar>

            <Box flexGrow={1} position="relative">
              <EventList />
            </Box>
          </Paper>
        </Box>

        <ThingDialog />
      </Box>
    )
  }
}

const styles = theme => ({
  root: {},
  dialogContainer: {
    alignItems: 'start',
  },
  dialogPaper: {
    marginTop: 56 + 16,
  },
  dialogBar: {
    backgroundColor: 'rgba(25,25,25,1)',
  },
  dialogContent: {
    padding: theme.spacing(3, 0),
  },
  dialogActions: {
    padding: theme.spacing(1),
    justifyContent: 'flex-start',
  },
  dialogAction: {
    padding: theme.spacing(1, 3),
  },
})

export default withStyles(styles)(Dispatch)
